*,*::before,*::after{box-sizing:border-box}html,body{margin:0;padding:0}html{font-size:100%}body{position:relative;min-height:100vh;overflow-x:hidden;background-color:#474747;color:#e3d5ba}body,button,input,textarea{font-family:"Dosis", sans-serif}h1,p{text-align:center}h1{font-size:1.8em;line-height:1.1;margin:0 0 0.3em}p{margin:0 0 0.3em}a{color:#e86701;text-decoration:none}a:hover{color:#e3d5ba;transition:color 0.15s ease}small{font-size:60%}hr{width:100%;border:1px dashed #e86701}label,input{display:inline-block;margin:0 0 0.3em;width:100%;max-width:300px}.form-control{margin-bottom:1em}label{color:#e86701;font-weight:bold}form{text-align:center}button{font-size:1em;display:inline-block;color:#e86701;border:2px solid #e86701;background:none;margin:0 0.5em 0.5em 0;padding:0.2em 0.5em;border-radius:5px}
